.div-body {
  min-height: 140px;
  height:auto;
  width:100%;
  padding: 20px;
  border-width: 1px;
  border-style: solid;
  border-color: #C9C7C5;
 margin:25px 10px 0px 10px;
 border-radius: 7.5px;
 color:#706E6B;
 font-size:14px;
 font-weight:500;
}
.active .profilelink{
  background: rgb(244, 244, 244);
  padding: 5px;
  width: 100%;
  height: 100px;
}
.inactive .profilelink{
background:none;
}
.noshadow .MuiPaper-rounded{
  box-shadow:0px 0px 0px;
}
.noshadow .active{
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
}
p.logo{
  text-align:center;
}