.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#root{
  width:90%;
  padding:72px;
}
.profileitem p{
  font-size:14px;
  font-family: 'Lato', sans-serif;
}
.MuiGrid-item a{
  color:#303133;
  text-decoration:none;
  display: block;
  width: 100%;
  height: 100%;
}
.MuiGrid-item.tile{
  width:20%;
}
.profilelink{
  width:100%;
  height:100%;
  border:0px;
  background-color:transparent;
  padding:0px;
  cursor:pointer;
}
.profilelink:focus{
  outline:0;
}
.MuiDialog-paperScrollPaper{
  width:95%;
  height:95%;
}
.MuiDialogTitle-root h6{
  text-align:center;
}
.MuiDialogContent-root .MuiGrid-container{
height:100%;
}
.frameButton{
 background-color: #2d9cdb;
    color: #ffffff;
    text-transform:none;
    padding: 6px 12px;
    min-width: 80px;
    margin-right:20px;
   font-size:13px;
    font-weight:400;
    border:0px;
    border-radius:4px;
}
.DialogContent{
  flex:none !important;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.MuiTabs-root .MuiButtonBase-root:not(.Mui-selected){
  pointer-events: none;
}

@media only screen and (max-width: 1024px) and (min-width: 768px){
  #root {
    width:auto !important;
   }
   .MuiGrid-grid-xs-3{
    max-width: 33% !important;
    flex-basis: 33% !important;
   }
   .profileitem p{
     font-size:12px !important;
   }
   .gridWrapper .MuiGrid-root.MuiGrid-item.MuiGrid-grid-xs-6{
     max-width:100% !important;
     width:100% !important;
   }
   .gridWrapper .MuiGrid-container{
     display:block !important;
   }
   
}

@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : landscape) { 
  .gridWrapper{
    min-height:280vh;
  }
  .sidebar .MuiPaper-root > div{
    height:220px !important;
  }
}

@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : portrait) { 
  .gridWrapper{
    min-height:200vh;
  }
  .reventTable h5{
    font-size:20px !important;
  }
 }


